.loading-box{
    width: 100%;
    height: 100%;
    line-height: 80vw;
    text-align: center;
    // background: url(https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570768298097&di=241509b8441714ba623773dc8c8a4388&imgtype=0&src=http%3A%2F%2Fimg.ui.cn%2Fdata%2Ffile%2F2%2F4%2F3%2F1362342.gif%3FimageMogr2%2Fauto-orient%2Fstrip%2Fthumbnail%2F%2521900%253E) no-repeat;
    background-size: 50%;;
    background-position: top;
    // mix-blend-mode: multiply;
    font-size: .12rem;
    color: #757575;
    .img-box{
        position: absolute;
        width: 1.00rem;
        height:1rem;
        // left: 0;
        // right: 0;
        // bottom: 0;
        // top: 0;
        // margin: auto;
        left: calc(50vw - 50px);
        top: calc(40vh - 50px);
        transition: none;
        background: url(data:image/png;base64,);
        background-size: auto 1rem;
        animation: cat 2s infinite step-start;
    }
    @keyframes cat {
        0% {
            background-position: 0 0;
        }
        4% {
            background-position: -200px 0;
        }
        8% {
            background-position: -400px 0;
        }
        12% {
            background-position: -600px 0;
        }
        16% {
            background-position: -800px 0;
        }
        20% {
            background-position: -1000px 0;
        }
        24% {
            background-position: -1200px 0;
        }
        28% {
            background-position: -1400px 0;
        }
        32% {
            background-position: -1600px 0;
        }
        36% {
            background-position: -1800px 0;
        }
        40% {
            background-position: -2000px 0;
        }
        44% {
            background-position: -2200px 0;
        }
        48% {
            background-position: -2400px 0;
        }
        52% {
            background-position: -2600px 0;
        }
        56% {
            background-position: -2800px 0;
        }
        60% {
            background-position: -3000px 0;
        }
        64% {
            background-position: -3200px 0;
        }
        68% {
            background-position: -3400px 0;
        }
        72% {
            background-position: -3600px 0;
        }
        76% {
            background-position: -3800px 0;
        }
        80% {
            background-position: -4000px 0;
        }
        84% {
            background-position: -4200px 0;
        }
        88% {
            background-position: -4400px 0;
        }
        92% {
            background-position: -4600px 0;
        }
        96% {
            background-position: -4800px 0;
        }
        100% {
            background-position: 0 0;
        }
    }
}